window.utils = {
	 
	countryList: function(countries){
		return countries;
	},

	stateList: function(array, countryId){
		var country = _.findWhere(array, { name: countryId });
                                    
                        return country ? country.states : '';
	},

	cityList: function(array, countryId, indexValueOfCountry, indexValueOfState) {
	    var indexOfCountry = -1,
	    	indexOfState = -1;

	                        
	    _.each(array, function(data, countryIds) { 
			if (_.isEqual(data.name, indexValueOfCountry)) {
					indexOfCountry = countryIds;
				return indexOfCountry;
			 } 
			});

	                         
		_.each(array, function(data) { 
				return _.each(data.states, function(statesData, statesId){
					if (_.isEqual(statesData.name, indexValueOfState)) {
							indexOfState = statesId;
						return indexOfState;
					}

				});

		});		

		var country = _.findWhere(array, { name: countryId }); 
						   
                        return country ? array[indexOfCountry].states[indexOfState].cities : '';
				  			 
	},
	ageOfCompany: function(numberOfYears){
		var i;
		var years = [];
			 
		for (i=1; i<=numberOfYears; i++){
			years.push(i);
		}
		return years;
	},

    dateValidator: function(firstValue, secondValue){
        var firstDateValue = firstValue.split('-');
        var secondDateValue= secondValue.split('-');

        var firstDate=new Date();
            firstDate.setFullYear(firstDateValue[0],(firstDateValue[1] - 1 ),firstDateValue[2]);

        var secondDate=new Date();
            secondDate.setFullYear(secondDateValue[0],(secondDateValue[1] - 1 ),secondDateValue[2]);     

            if (firstDate > secondDate){
                utils.showAlert('ERROR:', 'Date of Issue cannot be greater than Date of Expiry.', 'modal-danger');
                $( '#dateOfIssue' ).val(''); 
                $('.dateOfIssue-error').addClass("has-error");
            }       
    },   

	showAlert: function(title, text, klass) {
	    $('.alert').removeClass("alert-error alert-warning alert-success alert-info");
	    $('.alert').addClass(klass);
	    $('.alert').html('<strong>' + title + '</strong> ' + text);
	    $('.alert').show();
		    if(klass == "modal-success"){
		        	this.alertSucess();
		    }
	    $("#error-warning").modal('show');
	},

	alertSucess: function (){
	   	$('.error-header').removeClass("modal-header");
	    $('.error-header').addClass("modal-validation-success-header");
	    $('.error-body').removeClass("modal-body");
	    $('.error-body').addClass("modal-validation-success-body");
	    $('.sub-warning-title').html("Success");
	},

	formAnimate: function (height, speed){
		$('html, body').animate({scrollTop: height}, speed);
	},
	hideAlert: function() {
	   $('.alert').hide();
	}
};
